import React, { useEffect, useState } from "react";

import "./AboutInterac8.css";

import { useNavigate } from "react-router-dom";

export default function AboutInterac8() {
  const navigate = useNavigate();

  const [screenWidth, setScreenWidth] = useState(window.screen.width);

  useEffect(() => {
    const handleResize = () => {
      setScreenWidth(window.screen.width);
    };

    window.addEventListener("resize", handleResize);

    // Clean up the event listener
    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);

  return (
    <>
      <div className="about-us-banner"></div>

      <br />
      <br />
      <br />

      <div className="about-us-container">
        <div className="about-1">
          <div>
            <h1>About Us</h1>
            <br />
          </div>

          <p>
            We are the USA-based Digital Marketing Agency, we specialize in a
            range of services including search engine optimization (SEO),
            campaigns, branding, and more that reach and engage audiences across
            all digital platforms. At Interac8, we provide top-notch digital
            marketing services for businesses throughout the United States and
            we believe in crafting digital experiences that mix creativity and
            strategic direction, we have successfully helped countless
            businesses reach their online goals with innovative strategies
            tailored to your needs, and our team of strategists, developers, and
            creatives work to equip you with the tools needed for success in
            todayâ€™s ever-changing trends!
          </p>

          <br />
          <br />

          {screenWidth > 1280 && (
            <div className="about-us-buttons">
              <button
                onClick={() => {
                  navigate("/portfolio");
                }}
              >
                Portfolio
              </button>

              <button
                onClick={() => {
                  navigate("/contactus");
                }}
              >
                Contact Us
              </button>
            </div>
          )}
        </div>

        <div className="about-2">
          <img src="https://res.cloudinary.com/dx3u24xir/image/upload/f_auto,q_auto/v1692439857/AboutPage/about-banner-3_c6fn85.webp" alt="banner three" />
        </div>

        {screenWidth <= 1280 && (
          <div className="about-us-buttons">
            <button>Portfolio</button>
            <button>Contact Us</button>
          </div>
        )}
      </div>
    </>
  );
}
